<template>
	<view class="container">
		<view class="order-box">
			<tui-sticky :scrollTop="scrollTop" stickyHeight="80rpx">
				<template v-slot:header>
					<view class="order-class">
						<tui-tabs :tabs="tabs" :currentTab="currentTab" @change="orderClassChange"></tui-tabs>
					</view>
				</template>
			</tui-sticky>
			<view class="order-content-box">
				<view class="order-all" v-show="currentTab == 0 ? true : false">
					<orderAll />
				</view>
				<view class="order-paid" v-show="currentTab == 1 ? true : false">
					<orderPaid />
				</view>
				<view class="order-shipped" v-show="currentTab == 2 ? true : false">
					<orderShipped />
				</view>
				<view class="order-received" v-show="currentTab == 3 ? true : false">
					<orderReceived />
				</view>
				<view class="order-complete" v-show="currentTab == 4 ? true : false">
					<orderComplete />
				</view>
				<view class="order-after-sales" v-show="currentTab == 5 ? true : false">
					<afterSales />
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import orderAll from '@/pages/personal-pages/order/orderAll/orderAll.vue'
	import orderPaid from '@/pages/personal-pages/order/orderPaid/orderPaid.vue'
	import orderShipped from '@/pages/personal-pages/order/orderShipped/orderShipped.vue'
	import orderReceived from '@/pages/personal-pages/order/orderReceived/orderReceived.vue'
	import orderComplete from '@/pages/personal-pages/order/orderComplete/orderComplete.vue'
	import afterSales from '@/pages/personal-pages/order/afterSales/afterSales.vue'
	export default {
		components: {
			orderAll,
			orderPaid,
			orderShipped,
			orderReceived,
			orderComplete,
			afterSales
		},
		data() {
			return {
				scrollTop: 0,
				currentTab: 0,
				tabs: [{
					name: "全部"
				}, {
					name: "待付款"
				}, {
					name: "待发货"
				}, {
					name: "待收货"
				}, {
					name: "已完成"
				}, {
					name: "售后"
				}]
			};
		},
		onPageScroll(e) {
			this.scrollTop = e.scrollTop
		},
		onLoad(option) {
			// 获取当前标签页
			this.currentTab = decodeURIComponent(option.currentTabIndex)
		},
		methods: {
			orderClassChange(e) {
				this.currentTab = e.index
			}
		}
	}
</script>

<style lang="scss">
	.container {
		width: 100%;
		height: 100vh;
		background-color: #f6f6f6;
		display: flex;
		flex-direction: column;

		.order-content-box {
			width: 100%;
			display: flex;
			margin: 0 auto;
			margin-top: 10rpx;
			border-radius: 8rpx;
			background-color: #f6f6f6;

			.order-all {
				width: 100%;
			}

			.order-paid {
				width: 100%;
			}

			.order-shipped {
				width: 100%;
			}

			.order-received {
				width: 100%;
			}

			.order-complete {
				width: 100%;
			}
			
			.order-after-sales{
				width: 100%;
			}
		}
	}
</style>
